import { NextResponse } from "next/server";
import { PrismaClient } from "@prisma/client";
import { getServerSession } from "next-auth";
import { authOptions } from "@/app/api/auth/[...nextauth]/route";

const prisma = new PrismaClient();

// ‚úÖ GET: Fetch tasks
export async function GET() {
  const session = await getServerSession(authOptions);
  if (!session) return NextResponse.json({ error: "Unauthorized" }, { status: 401 });

  try {
    console.log("Fetching tasks for user:", session.user.id);
    const tasks = await prisma.task.findMany({
      where: {
        OR: [
          { userId: session.user.id }, // Personal tasks
          { teamMemberId: session.user.id }, // Team tasks
        ],
      },
      orderBy: { createdAt: "desc" },
    });

    return NextResponse.json(tasks);
  } catch (error) {
    console.error("‚ùå Error fetching tasks:", error);
    return NextResponse.json({ error: "Internal Server Error" }, { status: 500 });
  }
}

// ‚úÖ POST: Create a new task
export async function POST(req: Request) {
  const session = await getServerSession(authOptions);
  if (!session) return NextResponse.json({ error: "Unauthorized" }, { status: 401 });

  try {
    const { title, description, points, type, teamMemberId } = await req.json();

    if (type === "team" && !teamMemberId) {
      return NextResponse.json({ error: "Team task must have an assigned team member" }, { status: 400 });
    }

    const task = await prisma.task.create({
      data: {
        title,
        description,
        points: points ?? 10,
        type,
        userId: session.user.id,
        teamMemberId: type === "team" ? teamMemberId : null,
      },
    });

    console.log(`‚úÖ Task created: ${task.title} (${task.type})`);
    return NextResponse.json(task, { status: 201 });
  } catch (error) {
    console.error("‚ùå Error creating task:", error);
    return NextResponse.json({ error: "Failed to create task" }, { status: 500 });
  }
}

// ‚úÖ PUT: Toggle task completion status
export async function PUT(req: Request) {
  try {
    const session = await getServerSession(authOptions);
    if (!session || !session.user) {
      console.log("‚ùå User not authenticated");
      return NextResponse.json({ error: "Not authenticated" }, { status: 401 });
    }

    const { id, completed } = await req.json();
    if (!id) {
      console.log("‚ùå Task ID missing");
      return NextResponse.json({ error: "Task ID is required" }, { status: 400 });
    }

    const task = await prisma.task.findUnique({ where: { id } });
    if (!task) {
      console.log(`‚ùå Task with ID ${id} not found`);
      return NextResponse.json({ error: "Task not found" }, { status: 404 });
    }

    console.log(`‚úÖ Found task: ${task.title}, Type: ${task.type}, Completed: ${task.completed}`);

    // ‚úÖ Update task completion status in the database
    const updatedTask = await prisma.task.update({
      where: { id },
      data: { completed },
    });

    console.log(`‚úÖ Task updated: Completed -> ${updatedTask.completed}`);

    // ‚úÖ If it's a team task, update BOTH users' points
    if (task.type === "team" && task.teamMemberId) {
      console.log(`üîÑ Updating points for Team Task. Users: ${task.userId}, ${task.teamMemberId}`);

      await prisma.user.updateMany({
        where: {
          OR: [{ id: task.userId }, { id: task.teamMemberId }],
        },
        data: {
          points: { [completed ? "increment" : "decrement"]: task.points },
        },
      });

      console.log(`‚úÖ Points updated for both users`);
    } else {
      console.log(`üîÑ Updating points for personal task, User: ${session.user.id}`);

      await prisma.user.update({
        where: { id: session.user.id },
        data: { points: { [completed ? "increment" : "decrement"]: task.points } },
      });

      console.log(`‚úÖ Points updated for user ${session.user.id}`);
    }

    return NextResponse.json(updatedTask);
  } catch (error) {
    console.error("‚ùå Error updating task:", error);
    return NextResponse.json({ error: "Internal Server Error" }, { status: 500 });
  }
}

// ‚úÖ DELETE: Delete a task
export async function DELETE(req: Request) {
  try {
    const session = await getServerSession(authOptions);
    if (!session || !session.user) {
      return NextResponse.json({ error: "Not authenticated" }, { status: 401 });
    }

    const { id } = await req.json();
    if (!id) {
      return NextResponse.json({ error: "Task ID is required" }, { status: 400 });
    }

    const task = await prisma.task.findUnique({ where: { id } });
    if (!task) {
      return NextResponse.json({ error: "Task not found" }, { status: 404 });
    }

    await prisma.task.delete({ where: { id } });
    console.log(`üóëÔ∏è Task deleted: ${task.title}`);

    return NextResponse.json({ message: "Task deleted successfully" });
  } catch (error) {
    console.error("‚ùå Error deleting task:", error);
    return NextResponse.json({ error: "Internal Server Error" }, { status: 500 });
  }
}
